/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.container {
  height: 100vh;
  background-color: #e4edf6;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.container .cover {
  display: flex;
  justify-content: center;
  height: 50%;
  width: 100%;
}
.container .cover .stillImg {
  width: 70%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  filter: blur(3px);
}
.container .main {
  background-color: white;
  box-shadow: 0 5px 20px 5px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 50%;
  width: 70%;
}
.container .main .poster {
  z-index: 1;
  position: absolute;
  top: 45%;
  left: 10%;
  width: 212px;
  height: 300px;
  box-shadow: 0 5px 20px 5px rgba(0, 0, 0, 0.5);
}
.container .main .title {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 15px 0 15px 150px;
}
.container .main .title h1 {
  font-size: 1.6rem;
  font-weight: 600;
  width: 33.3333333333%;
  text-align: center;
}
.container .main .title span {
  font-size: 20px;
  font-weight: 600;
  width: 33.3333333333%;
  text-align: center;
}
.container .main .plot {
  width: 100%;
  padding: 0 0 0 150px;
}
.container .main .plot p {
  font-size: 1rem;
  font-weight: 400;
  width: 90%;
  text-align: justify;
  line-height: 1.5;
}
.container .main .divider {
  position: relative;
  top: 10%;
  width: 80%;
  height: 1px;
  background-color: #e4edf6;
}
.container .main .actor {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 40px;
  padding: 60px 20px 0 20px;
}
.container .main .actor p {
  padding-right: 10px;
}
.container .main .genre {
  width: 100%;
  display: flex;
  font-size: 1.5rem;
  justify-content: space-around;
  margin-top: 15px;
  font-weight: 600;
}/*# sourceMappingURL=MovieInfo.module.css.map */